<template>
  <div class="my-dialog" v-if="isShow">
    <div class="shade" @click="$emit('update:isShow',false)"></div>
    <div class="dialog-wrap">
      <div class="section">
        <div class="dialog anim-scale">
          <slot name="header">
            <div class="header">
              <!-- <img class="dialog-close" @click="$emit('update:isShow',false)" src="@/assets/images/close@2x.png" alt=""> -->
              <div v-if="title" class="title">{{title}}</div>
            </div>
          </slot>
          <div class="content">
            <slot name="content">
              <div v-if="content">{{content}}</div>
            </slot>
          </div>
          <div class="footer">
            <slot name="footer">

            </slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs, watch } from "vue";
import { useI18n } from "vue-i18n";
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: ''
    },
  },
  components: {
  },
  setup (props) {
    const { t } = useI18n()
    const data = reactive({
      t: t
    })
    watch(
      () => props.isShow,
      (newVal) => {
        console.log(newVal, 'my-dialog')
        if (newVal) {
          document.querySelector('body').style.overflow = 'hidden'
        } else {
          document.querySelector('body').removeAttribute('style')
        }
      }
    )
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.my-dialog {
  position: relative;
  z-index: 999;
  .shade {
    background-color: rgba(0, 0, 0, 0.5);
    pointer-events: auto;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .dialog-wrap {
    display: table;
    font-family: Helvetica, arial, sans-serif;
    pointer-events: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    .section {
      display: table-cell;
      vertical-align: middle;
      text-align: center;
    }
    .dialog {
      line-height: 1.3;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background: #fff;
      width: 80% !important;
      border: none;
      color: #111;
      max-width: 640px;
      position: relative;
      display: inline-block;
      text-align: left;
      background-color: #fff;
      font-size: 14px;
      border-radius: 10px;
      box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
      pointer-events: auto;
      -webkit-overflow-scrolling: touch;
      -webkit-animation-fill-mode: both;
      animation-fill-mode: both;
      -webkit-animation-duration: 0.2s;
      animation-duration: 0.2s;
      .header {
        position: relative;
        .dialog-close {
          right: 7px;
          top: 7px;
          position: absolute;
          box-sizing: content-box;
          width: 12px;
          height: 12px;
          padding: 8px;
        }
      }
      .title {
        font-size: 17px;
        color: #111;
        text-align: center;
        font-weight: 600;
        padding-top: 20px;
      }
      .content {
        padding: 28px 20px 15px;
        text-align: center;
        font-size: 14px;
        color: #111;
      }
    }
  }
  @-webkit-keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  @keyframes anim-scale {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }

    100% {
      opacity: 1;
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  .anim-scale {
    animation-name: anim-scale;
    -webkit-animation-name: anim-scale;
  }
}
</style>
<style lang="less">
.my-dialog {
  .dialog-li {
    display: flex;
    padding-bottom: 21.5px;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    &:last-child {
      padding-bottom: 0;
    }
    .input-wrap {
      padding: 0 15px;
      display: flex;
      align-items: center;
      width: 100%;
      .input-label {
        font-size: 14px;
        font-weight: 600;
        color: #0d469f;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        .arrow {
          width: 14px;
          height: 8.5px;
          display: block;
          margin-left: 4.5px;
        }
      }
      input {
        height: 50px;
        padding: 0 10px 0 0;
        border: 0;
        font-size: 14px;
        font-weight: 600;
        color: #333;
        width: 100%;
      }
    }
    .input-info {
      width: 100%;
      text-align: right;
      margin-top: 15px;
    }
    .select-wrap {
      select {
        border: 0;
        font-size: 15px;
        color: #111;
        text-align: right;
        &:focus-visible {
          outline: 0;
        }
      }
    }
    .key {
      font-size: 15px;
      color: #666;
    }
    .value {
      font-size: 15px;
      color: #111;
      word-break: break-all;
      text-align: left;
      font-weight: 600;
    }
  }
  .dialog-des {
    font-size: 13px;
    text-align: left;
  }
  .dialog-btn {
    margin-top: 0;
    padding: 12px 20px 20px;
    .van-button {
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      border-radius: 10px;
      &:nth-child(1):last-child {
        width: calc(100% - 50px);
        display: block;
        margin: 0 auto;
      }

      &:nth-child(2):last-child {
      }
    }
  }
}
</style>
