<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="{ active: headerActive }" left-arrow>
      <template #left>
        <template v-if="type === 'back'">
          <img class="back" src="@/assets/images/back2@2x.png" @click="$goBack" alt="" />
        </template>
        <template v-else>
          <img class="logo" style="width:23px;hegiht:auto;display:block;" src="@/assets/images/logo.png" alt="">
          <img class="logo-text" style="width:54px;hegiht:auto;display:block;margin:0 6px;" src="@/assets/images/websitename.png" alt="">
          <div class="address" v-if="address.length">
            {{address.substring(0, 4)}}***{{address.substring(address.length - 4, address.length)}}
          </div>
        </template>
      </template>
      <template #right>
        <van-popover v-if="type === 'home'" v-model:show="showPopover" placement="bottom-end" :actions="actions" @select="changeLanuage">
          <template #reference>
            <div class="flex-center">
              {{localFilter(local)}}
              <img src="@/assets/images/arrow_down@2x.png" class="arrow" alt="">
            </div>
          </template>
        </van-popover>
      </template>
    </van-nav-bar>
    <MyDialog v-model:isShow="isDialogIntro" :title="t('index.')">
      <template #content>
        <div class="dialog-li">
          <div class="input-wrap border">
            <input type="text" v-model="intro" :placeholder="t('index.')">
            <div class="input-label">
              <van-popover v-model:show="showPopoverIntro" placement="bottom-end" :actions="actionsIntro" @select="changeIntroArea">
                <template #reference>
                  <div class="flex-center">
                    {{curAction.text}}
                    <img class="arrow" src="@/assets/images/arrow-down_2@2x.png" alt="">
                  </div>
                </template>
              </van-popover>
            </div>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn">
          <van-button @click="register" type="primary">{{t('index.')}}</van-button>
        </div>
      </template>
    </MyDialog>
  </div>
</template>

<script>
import { reactive, toRefs, computed, onMounted, watch } from "vue";
import Clipboard from "clipboard";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang, setLang } from "@/utils/i18n";
import { onBeforeRouteLeave } from "vue-router";
import { Toast } from "vant";
import MyDialog from '@/components/MyDialog'
import { getUser, register, getNonce } from "@/api"
import { intro, region } from "@/storage"
export default {
  components: {
    MyDialog
  },
  props: {
    type: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  setup (props) {
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts),
      web3: computed(() => store.state.provider),
      type: props.type,
      headerActive: 0,
      title: props.title,
      local: getLang() || "zh-TW",
      curOrigin: location.origin,
      showPopover: false,
      actions: [
        {
          text: 'English',
          value: 'en'
        },
        {
          text: '',
          value: 'zh-TW'
        },
        {
          text: '',
          value: 'ja'
        },
        {
          text: '',
          value: 'ko'
        }
      ],
      intro: intro.get() || '',
      isDialogIntro: false,
      showPopoverIntro: false,
      actionsIntro: [
        {
          text: t('index.'),
          value: '1'
        },
        {
          text: t('index.'),
          value: '2'
        }
      ],
      curAction: region.get() == '2' ? {
        text: t('index.'),
        value: '2'
      } : {
        text: t('index.'),
        value: '1'
      },
      getUser: () => {
        getUser({ address: data.address }).then(() => {
          data.isDialogIntro = false
        }).catch(() => {
          console.log(111111)
          data.isDialogIntro = true
        })
      },
      register: async () => {
        if (!data.intro) {
          Toast(t('index.'))
          return
        }
        let nonceInfo = await getNonce({ address: data.address });
        let nonce = nonceInfo.nonce;
        let signature = await data.web3.trx.sign(data.web3.toHex(nonce));
        register({ address: data.address, intro_address: data.intro, region: data.curAction.value, signature: signature }).then(() => {
          data.isDialogIntro = false
          window.location.reload()
        })
      },
      changeLanuage: val => {
        setLang(val.value);
        data.local = val.value;
        data.lactionsIntro = [
          {
            text: t('index.'),
            value: '1'
          },
          {
            text: t('index.'),
            value: '2'
          }
        ]
        data.curAction = region.get() == '2' ? {
          text: t('index.'),
          value: '2'
        } : {
          text: t('index.'),
          value: '1'
        }
        location.reload()
      },
      changeIntroArea: (value) => {
        data.curAction = value
      },
      localFilter (value) {
        let list = data.actions
        let res = ''
        for (let k = 0; k < list.length; k++) {
          if (list[k].value === value) {
            res = list[k].text
            break
          }
        }
        return res
      }
    });
    if (data.address) {
      data.getUser()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.getUser()
      }
    })
    onMounted(() => {
      var clipboard = new Clipboard(".copy");
      clipboard.on("success", function (e) {
        Toast(t("share."));
        e.clearSelection();
      });
      clipboard.on("error", function () {
        Toast(t("share."));
      });
      var clipboardHref = new Clipboard(".copy-cur-href");
      clipboardHref.on("success", function (e) {
        Toast(t("share."));
        e.clearSelection();
      });
      clipboardHref.on("error", function () {
        Toast(t("share."));
      });
    });
    document.addEventListener("scroll", function () {
      let scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop;
      data.headerActive = scrollTop > 30;
    });
    onBeforeRouteLeave(() => {
      document.querySelector("body").style.overflow = "auto";
    });
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.wallet-block {
  border: 1px solid #d2d9e1;
  border-radius: 5px;
  padding: 6px 12px;
  width: 100%;
  .wallet-img {
    width: 19px;
    height: 19px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .address {
    overflow: hidden;
    font-size: 14px;
    color: #111;
    word-break: break-all;
  }
}
.menu-Block {
  position: fixed;
  left: 0;
  right: 0;
  top: 44px;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 499;
  .menu-leftBlock {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background: #fff;
    right: 31%;
    .menu-item {
      padding: 14px 14px;
      .menu-img {
        width: 21px;
        height: 21px;
      }
      .menu-tit {
        font-size: 14px;
        color: #222222;
        margin-left: 20px;
        span {
          font-size: 16px;
          color: #ff9600;
        }
      }
    }
  }
}
.back {
  width: 9px !important;
  height: 16px;
}
.header-icon {
  width: 23px;
  height: 23px;
  display: block;
}
.copy {
  width: 16px;
  height: 16px;
}
.menu-block {
  padding: 0 15px;
  .title {
    font-size: 15px;
    color: #111;
    font-weight: 600;
    margin-bottom: 10px;
    margin-top: 13px;
  }
}
.arrow {
  width: 7.5px;
  height: 4px;
  display: block;
  margin-left: 4px;
}
.address {
  display: block;
  padding: 8px 14px;
  line-height: 1;
  font-size: 12px;
  color: #fff;
  word-break: break-all;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  margin-right: 12px;
}
</style>
<style lang="less">
#app {
  .van-nav-bar {
    background: #000000;
  }
}
</style>
